* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
  }

  @font-face {
    font-family: "Housttely Signature";
    src: url("./fonts/HousttelySignature.ttf");
    font-display: swap;
  }

  @font-face {
    font-family: "Roboto";
    src: url("./fonts/Roboto-Regular.ttf");
    font-display: swap;
  }

  @font-face {
    font-family: "Qanelas Soft DEMO";
    src: url("./fonts/QanelasSoftDEMO-ExtraBold.otf");
    font-display: swap;
  }
  
  
  body {
    background: var(--color-bg);
  }
  
  a {
    color: unset;
    text-decoration: none;
  }
  
  .gray__bg {
    background: #FAFAFA;
  }

  .black__bg {
    background: #0E0E0E;
  }
  
  .gradient__bg {
  
    border-bottom-left-radius: 8rem;
    border-bottom-right-radius: 8rem;
  
    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 24% 27%, rgba(0, 0, 0, 1) 0%, rgba(16, 16, 16, 1) 50%, rgba(26, 30, 63, 1) 100%);
  
    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 24% 27%, rgba(0, 0, 0, 1) 0%, rgba(16, 16, 16, 1) 50%, rgba(26, 30, 63, 1) 100%);
  
    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 24% 27%, rgba(0, 0, 0, 1) 0%, rgba(16, 16, 16, 1) 50%, rgba(26, 30, 63, 1) 100%);
  
    /* ie 10+ */
    background:-ms-radial-gradient(circle at 24% 27%, rgba(0, 0, 0, 1) 0%, rgba(16, 16, 16, 1) 50%, rgba(26, 30, 63, 1) 100%);
  
    /* global 92%+ browsers support */
    background:radial-gradient(circle at 24% 27%, rgba(0, 0, 0, 1) 0%, rgba(16, 16, 16, 1) 50%, rgba(26, 30, 63, 1) 100%);
  
  }
  
  .gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .section__padding {
    padding-left: 4rem;
    padding-top: 6rem;
  }
  
  .section__margin {
    margin: 4rem 6rem;
  }
  
  .scale-up-center {
      -webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
              animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  }
  
  @-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @media screen and (max-width: 700px) {
    .section__padding {
      padding: 4rem;
    }
  
    .section__margin {
      margin: 4rem;
    }
  
    .gradient__bg {
      border-bottom-left-radius: 6rem;
      border-bottom-right-radius: 6rem;
    }
  }
  
  @media screen and (max-width: 550px) {
    .section__padding {
      padding: 4rem 2rem;
    }
    
    .section__margin {
      margin: 4rem 2rem;
    }
  
    .gradient__bg {
      border-bottom-left-radius: 4rem;
      border-bottom-right-radius: 4rem;
    }
  }
  
        